import React from 'react'

export const ChinhSachBaoMat = () => {
    return (
        <div className="p-5" style={{ background: "white" }}>
            <h1 className='text-center font-bold text-3xl p-3'>CHÍNH SÁCH BẢO MẬT</h1>
            <div className="entry-content">
                <p>Công ty TNHH Cybersoft  tôn trọng và cam kết bảo vệ dữ liệu cá nhân của người dùng. Chúng tôi cam kết sẽ thực hiện các biện pháp liên quan để bảo vệ dữ liệu cá nhân của người dùng tuân theo các quy định pháp luật áp dụng về bảo mật thông tin/dữ liệu cá nhân. Quy chế Quyền riêng tư này (“Quy chế”) được lập nhằm quy định cách thức chúng tôi thu thập, sử dụng, tiết lộ, lưu trữ và/hoặc xử lý dữ liệu khi bạn truy cập vào trang web cybersoft.edu.vn, ứng dụng Cybersoft trên nền tảng Android hoặc iOS chính thức của chúng tôi và tất cả các trang web phụ liên quan khác (“Nền tảng”).</p>
                <p>Bằng việc sử dụng các Dịch vụ, đăng ký Tài khoản với chúng tôi hoặc truy cập Nền tảng của chúng tôi, bạn xác nhận và đồng ý cho phép chúng tôi&nbsp;thu thập, sử dụng, tiết lộ và/hoặc xử lý dữ liệu cá nhân của bạn như quy định trong Quy chế này. NẾU BẠN KHÔNG ĐỒNG Ý CHO PHÉP XỬ LÝ DỮ LIỆU CỦA BẠN NHƯ QUY ĐỊNH TRONG QUY CHẾ NÀY, VUI LÒNG KHÔNG SỬ DỤNG CÁC DỊCH VỤ CỦA CHÚNG TÔI HOẶC TRUY CẬP NỀN TẢNG CỦA CHÚNG TÔI.</p>
                <p>Chúng tôi có toàn quyền sửa đổi, bổ sung, cập nhật Quy chế này tại từng thời điểm. Vui lòng thường xuyên kiểm tra lại Quy chế quyền riêng tư này để theo dõi bất kỳ cập nhật hoặc thay đổi nào đối với Quy chế này.</p>
                <p><strong>1. Chúng tôi sẽ thu thập những thông tin cá nhân nào?</strong></p>
                <p>1.1 Những thông tin cá nhân mà chúng tôi có thể thu thập bao gồm, nhưng không giới hạn các thông tin sau:</p>
                <p style={{ paddingLeft: 40 }}>a) Thông tin tài khoản như họ và tên, địa chỉ email, địa chỉ, giới tính, ngày sinh, ảnh hồ sơ, số điện thoại di động;</p>
                <p style={{ paddingLeft: 40 }}>b) Dữ liệu khi bạn truy cập và/hoặc sử dụng Nền tảng của chúng tôi như địa chỉ IP, các thông tin về trình duyệt, các địa chỉ bạn đã truy cập trên Nền tảng, thời gian bạn hoạt động trên Nền tảng của chúng tôi;</p>
                <p style={{ paddingLeft: 40 }}>c) Dữ liệu về vị trí như vị trí điện thoại di động của bạn và/hoặc bất kỳ thông tin vị trí nào như khi bạn chụp và chia sẻ vị trí của bạn với chúng tôi dưới dạng hình ảnh hoặc video của bạn và tải nội dung đó lên Nền tảng;</p>
                <p style={{ paddingLeft: 40 }}>d) Dữ liệu về lịch sử, nội dung hội thoại giữa bạn và (các) người dùng khác trên Nền tảng của chúng tôi khi trao đổi thông qua chức năng chat với người bán, bao gồm nhưng không giới hạn các thông tin dưới dạng văn bản, âm thanh, hình ảnh.</p>
                <p style={{ paddingLeft: 40 }}>e) Bất kỳ thông tin, dữ liệu nào khác được tiết lộ bởi bạn khi bạn đăng nhập, sử dụng các Dịch vụ và các Nền tảng của chúng tôi như sở thích của bạn trong việc nhận thông tin tiếp thị từ chúng tôi và/hoặc từ các bên thứ ba của chúng tôi.</p>
                <p style={{ paddingLeft: 40 }}>f) Bất kỳ dữ liệu, thông tin tổng hợp khác về nội dung người dùng sử dụng.</p>
                <p>1.2 Trong quá trình bạn sử dụng Nền tảng và Dịch vụ, chúng tôi có thể thu thập thông tin cá nhân của bạn trong các trường hợp sau:</p>
                <p style={{ paddingLeft: 40 }}>a) Khi bạn đăng ký và/hoặc mở một tài khoản với chúng tôi;</p>
                <p style={{ paddingLeft: 40 }}>b) Khi bạn đăng ký và/hoặc sử dụng bất kỳ Dịch vụ trên Nền tảng chúng tôi;</p>
                <p style={{ paddingLeft: 40 }}>c) Khi bạn sử dụng bất kỳ tính năng hoặc chức năng nào có sẵn trên Nền tảng của chúng tôi;</p>
                <p style={{ paddingLeft: 40 }}>d) Khi bạn sử dụng chức năng chat trên Nền tảng;</p>
                <p style={{ paddingLeft: 40 }}>e) Khi bạn đăng ký, tham gia vào bất kỳ cuộc khảo sát, cuộc thi, chiến dịch khuyến mãi hoặc chiến dịch tiếp thị nào của chúng tôi;</p>
                <p style={{ paddingLeft: 40 }}>f) Khi bạn đăng bất kỳ ý kiến, nhận xét nào về nội dung của người dùng khác được tải lên Nền tảng hoặc khi bất kỳ người dùng nào khác trên Nền tảng đăng bất kỳ nhận xét về nội dung của bạn đã tải lên Nền tảng;</p>
                <p style={{ paddingLeft: 40 }}>g) Khi bạn gửi khiếu nại cho chúng tôi hoặc khi bên thứ ba khiếu nại về bạn hoặc nội dung của bạn trên Nền tảng;</p>
                <p style={{ paddingLeft: 40 }}>h) Khi bạn gửi thông tin cá nhân của bạn cho chúng tôi vì bất kỳ lý do gì.</p>
                <p>1.3 Bạn đồng ý không cung cấp cho chúng tôi bất kỳ thông tin nào không chính xác hoặc gây hiểu nhầm. Bạn phải thường xuyên cập nhật thông tin cá nhân của mình và thông báo cho chúng tôi về bất kỳ thông tin nào không chính xác hoặc khi có sự thay đổi. Chúng tôi có quyền yêu cầu bạn cung cấp những tài liệu cần thiết để xác minh thông tin cá nhân do bạn cung cấp.</p>
                <p>1.4 Nếu bạn liên kết tài khoản của bạn trên Nền tảng của chúng tôi với tài khoản mạng xã hội của bạn hoặc sử dụng một số tính năng mạng xã hội nào của Cybersoft, chúng tôi có thể truy cập dữ liệu cá nhân về bạn mà bạn đã tự nguyện cung cấp cho các nhà cung cấp dịch vụ tài khoản mạng xã hội theo chính sách của họ và chúng tôi sẽ quản lý, sử dụng các dữ liệu cá nhân này của bạn theo Quy chế Quyền riêng tư này tại mọi thời điểm.</p>
                <p><strong>2. Mục đích thu thập thông tin cá nhân</strong></p>
                <p>Chúng tôi có thể thu thập, sử dụng, tiết lộ và/hoặc xử lý thông tin cá nhân của bạn vì một hoặc nhiều mục đích sau đây:</p>
                <ul>
                    <li>Để xác minh danh tính người dùng;</li>
                    <li>Để liên hệ cho mục đích hỗ trợ cung cấp Dịch vụ của chúng tôi;</li>
                    <li>Để cung cấp thông tin chính thức cho người mua liên hệ với bạn khi bán hàng;</li>
                    <li>Để kiểm tra và đánh giá dữ liệu trên Nền tảng của chúng tôi;</li>
                    <li>Để nhận diện người dùng và quản lý thông tin tài khoản;</li>
                    <li>Để xác định người dùng truy cập trên Nền tảng;</li>
                    <li>Để phát hiện và ngăn chặn hoạt động gian lận;</li>
                    <li>Để tiến hành các hoạt động nghiên cứu tiếp thị;</li>
                    <li>Để nghiên cứu và phân tích thông tin nhân khẩu họ;</li>
                    <li>Để gửi các thông báo mà chúng tôi cho rằng cần thiết hoặc bạn sẽ quan tâm, trừ khi bạn từ chối nhận những thông báo này;</li>
                    <li>Để phân tích và đánh giá giúp cải thiện và nâng cấp Dịch vụ của chúng tôi;</li>
                    <li>Việc lưu trữ lịch sử hội thoại được chúng tôi thực hiện trong một khoảng thời gian nhất định phù hợp với quy định pháp luật tùy từng thời điểm để phục vụ cho việc quản lý nội dung và cải thiện trải nghiệm của người dùng;</li>
                    <li>Và bất kỳ mục đích nào mà chúng tôi thông báo cho bạn tại thời điểm xin sự cho phép của bạn.</li>
                </ul>
                <p><strong>3. Các trường hợp chia sẻ thông tin cá nhân</strong></p>
                <p>Ngoài các thông tin khác được mô tả trong Quy chế này, chúng tôi có thể thu thập và chia sẻ các thông tin của bạn cho bên thứ ba dưới dạng ẩn danh. Các bên thứ ba sẽ chỉ được sử dụng để thu thập, cung cấp thông tin của bạn cho chúng tôi, và sử dụng dữ liệu của bạn dưới sự cho phép của chúng tôi trong phạm vi cần thiết.</p>
                <p>Các bên thứ 3 bao gồm:</p>
                <ul>
                    <li>Các đối tác cung cấp các dịch vụ xử lý phân tích dữ liệu mà chúng tôi thuê để phục vụ cho hoạt động kinh doanh của chúng tôi</li>
                    <li>Các đối tác cung cấp các dịch vụ liên quan đến việc gửi nhận thông báo (bằng SMS, email, cuộc gọi) đến bạn</li>
                    <li>Các đối tác trong hoạt động marketing</li>
                    <li>Các đơn vị được chỉ định bởi cơ quan pháp luật Việt Nam</li>
                    <li>Các đơn vị được chúng tôi yêu cầu để bảo vệ an toàn và an ninh cho Dịch vụ</li>
                    <li>Các chi nhánh và/hoặc công ty liên kết của chúng tôi</li>
                    <li>Và/hoặc các đối tác khác mà chúng tôi tiết lộ vì một trong Mục đích thu thập thông tin cá nhân và các bên thứ ba đó ngược lại họ sẽ thu thập và xử lý thông tin cá nhân của bạn vì một hoặc nhiều mục đích được đề cập trong Quy chế này.</li>
                </ul>
                <p>Ngoài ra, khi có yêu cầu của cơ quan nhà nước có thẩm quyền theo quy định pháp luật, chúng tôi có thể chia sẻ thông tin của bạn nếu là hành động cần thiết để tuân thủ theo yêu cầu của các cơ quan đó.</p>
                <p><strong>4. Cookies</strong></p>
                <p>Chúng tôi có thể sử dụng “Cookies” hoặc các tính năng khác để cho phép chúng tôi hoặc bên thứ ba thu thập hoặc chia sẻ thông tin cá nhân của bạn sẽ giúp chúng tôi cải thiện Nền tảng của mình và Dịch vụ do chúng tôi cung cấp.</p>
                <p>Chúng tôi có thể&nbsp;sử dụng cookie để bạn không phải nhập thông tin đăng nhập của mình cho mỗi lần vào Nền tảng.</p>
                <p>Thông tin duy nhất mà cookie có thể chứa là thông tin mà chính người dùng cung cấp. Một tập cookie không thể đọc dữ liệu bên ngoài ổ cứng của người dùng hoặc đọc các tập cookie được tạo ra bởi các trang web khác.</p>
                <p>Khi truy cập và sử dụng Nền tảng, bạn đồng ý cho chúng tôi sử dụng Cookie để lưu trữ thông tin trên thiết bị của bạn.</p>
                <p>Bạn có thể quản lý và xóa cookies thông qua cài đặt trình duyệt hoặc thiết bị của bạn. Tuy nhiên, vui lòng lưu ý rằng nếu bạn thực hiện thao tác này bạn có thể không sử dụng được các chức năng đầy đủ của Nền tảng hoặc các Dịch vụ của chúng tôi.</p>
                <p><strong>5. Truy cập, điều chỉnh thông tin cá nhân</strong></p>
                <ul>
                    <li>Bạn có thể xem và thay đổi các thông tin cá nhân tại trang Hồ sơ cá nhân.</li>
                    <li>Bạn có thể yêu cầu truy cập các thông tin khác chúng tôi thu thập từ bạn, bằng cách liên hệ với chúng tôi qua email được công bố ở dưới.</li>
                    <li>Bạn có thể yêu cầu chấm dứt việc sử dụng thông tin cá nhân bằng cách liên hệ với chúng tôi qua email được công bố ở dưới.</li>
                    <li>Bạn có thể xóa lịch sử hội thoại trong chức năng Chat với người bán.</li>
                    <li>Chúng tôi có thể từ chối bạn yêu cầu truy cập thông tin cá nhân nếu:
                        <ul>
                            <li>Thông tin chỉ dùng với mục đích phân tích và đánh giá.</li>
                            <li>Thông tin sử dụng cho mục đích giải quyết tranh chấp (ví dụ: thông tin cá nhân của người khác/đối tượng tranh chấp).</li>
                            <li>Thông tin tổn hại đến hoạt động thương mại &amp; cạnh tranh của chúng tôi.</li>
                            <li>Chi phí, công sức cho việc cung cấp dữ liệu này không tương xứng với lợi ích nhận được của chúng tôi hoặc của bạn.</li>
                        </ul>
                    </li>
                    <li>Chúng tôi có quyền xác định thông tin nào nằm trong phạm vi có thể từ chối.</li>
                </ul>
                <p><strong>6. Các phương thức bảo vệ thông tin cá nhân</strong></p>
                <ul>
                    <li>Chúng tôi đảm bảo rằng mọi thông tin thu thập được sẽ được lưu giữ an toàn, bằng các phương thức sau:
                        <ul>
                            <li>Giới hạn truy cập thông tin cá nhân bằng việc Đăng ký tài khoản.</li>
                            <li>Sử dụng sản phẩm công nghệ để ngăn chặn truy cập máy tính trái phép.</li>
                            <li>Xóa thông tin cá nhân của quý khách khi nó không còn cần thiết cho mục đích lưu trữ hồ sơ của chúng tôi.</li>
                        </ul>
                    </li>
                    <li>Chúng tôi sử dụng công nghệ mã hóa theo giao thức 128-bit SSL (secure sockets layer) khi xử lý tài khoản của bạn.</li>
                </ul>
                <p><strong>7. Lưu giữ thông tin cá nhân</strong></p>
                <p>Chúng tôi sẽ chỉ lưu giữ thông tin cá nhân của bạn trong thời gian do pháp luật yêu cầu hoặc cho đến khi mục đích thu thập thông tin đó vẫn còn theo Quy chế này.</p>
                <p><strong>8. Thay đổi Quy chế quyền riêng tư</strong></p>
                <p>Chúng tôi có quyền thay đổi, cập nhật Quy chế quyền riêng tư này bất kỳ lúc nào. Việc thay đổi này sẽ được thông báo trên Nền tảng của chúng tôi.</p>
                <p><strong>9. Rút lại sự đồng ý về việc tiếp tục sử dụng, thu thập dữ liệu cá nhân</strong></p>
                <p>Bạn có thể rút lại sự đồng ý cho phép thu thập, sử dụng và/hoặc tiết lộ dữ liệu cá nhân của bạn mà chúng tôi đang lưu giữ bằng cách gửi email theo thông tin bên dưới. Xin vui lòng lưu ý nếu bạn rút lại sự đồng ý có thể có nghĩa là chúng tôi sẽ không thể tiếp tục cung cấp Dịch vụ cho bạn và chúng tôi có thể cần phải chấm dứt quan hệ hiện tại của bạn với chúng tôi.</p>
                <p><strong>10. Thông tin liên hệ</strong></p>
                <p>Nếu bạn có bất kỳ thắc mắc, muốn truy cập, thay đổi hoặc xóa thông tin cá nhân, hoặc khiếu nại, vui lòng liên hệ với chúng tôi theo địa chỉ email: info@cybersoft.vn</p>
                <p>Bản Quy chế CHÍNH SÁCH BẢO MẬT này được chỉnh sửa lần cuối vào ngày: 01/12/2020</p>
            </div>


        </div>
    )
}
